.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #012169;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

.btn.btn-primary {
  margin-top: 50px;
  width: 250px;
  height: 50px;
  border-radius: 15px;
  font-size: 20px;
  text-align: center;
  line-height: 0px;
  background-color: #012169;
}

.btn.btn-basic{
  width: 150px;
  font-size: 15px;
  margin-top: 10px;
  border-radius: 10px;
  color: black;
  background-color: silver;
}

.btn.btn-secondary {
  /* margin-top: 50px; */
  margin: 10px;
  width: 250px;
  height: 250px;
  border-radius: 15px;
  font-size: 20px;
  text-align: center;
  line-height: 0px;
  background-color: grey;
  background-size: 100% 100%;
}

.left-layout{
  text-align: left;
  margin: 100px 100px 0px 100px;
  font-family: 'Roboto';
}

.centered-layout{
  text-align: center;
  margin: 100px 100px 0px 100px;
  font-family: 'Roboto';
}

.results{
  font-family: 'Roboto';
}